@import 'person';

.people{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 10px;
    padding-top: 30px;

    @media (width <= 1000) {
        grid-template-columns: repeat(4, 1fr);
    }

    @media (width <= 750) {
        grid-template-columns: repeat(2, 1fr);
    }
}